export default {	
  data(){
    return {
		id:''
	}
  },
  created(){
   this.id = _g.randStr(20);
  },
  methods:{
  	hide(){
  		document.body.style.position = "relative";
  		document.getElementById(this.id).style.display = "none";
  	},
  	show(){
  		document.body.style.position = "fixed";
  		document.getElementById(this.id).style.display = "block";
  	}
  },
}  



