
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Flexbox,
FlexboxItem,
Divider
},

props: {
prompt: {
type: String,
default: ""
},
title: {
type: String,
default: "消息提示"
},
cancelBtn: {
type: Object,
default: () => {
return {
text: "取消",
style: "",
onCancel: () => {}
};
}
},
okBtn: {
type: Object,
default: () => {
return {
text: "确定",
style: "color:#323233",
onOk: () => {}
};
}
},
}
};



